.feature {
  margin: 4rem 0;
}

.feature-container {
  gap: 2rem;
}

.feature--item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  gap: 2rem;
}

.feature--item .feature-content {
  text-align: center;
}

.feature--item .feature-content h3 {
  color: #004936;
  margin-bottom: 1rem;
  letter-spacing: 1px;
  font-size: 24px;
  line-height: 34px;
  font-weight: 700;
}

.feature--item .feature-content p {
  color: #4E4B66;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  letter-spacing: 0.75px;
}

@media (min-width: 992px) {
  .feature--item .feature-content h3 {
    font-size: 40px;
    line-height: 48px;
  }
}